document.addEventListener('DOMContentLoaded', function() {
    mostrarServices();
});

async function mostrarServices(){
    try{
        const userId = readCookie("userId");
        const myHeaders = new Headers();
        myHeaders.append("Authorization", "Bearer eyJhbGciOiJIUzI1NiJ9.eyJzdWIiOiJKRWR1YXJkb1ZDIiwiZXhwIjoxNjcxOTA3Njg1LCJpYXQiOjE2NjkzMTU2ODV9.4UavGrx2mUlFkFbCCniji3rX4QXCMj6xedTYtm6QwtM");
        const resultado = await fetch(`/api/user/${userId}`,{method: 'GET', headers: myHeaders, redirect: 'follow'});
        const client = await resultado.json();
        const { name, lastName } = client;
        const nombreUsuario = document.querySelector("#nombre-usuario");
        nombreUsuario.textContent = `${name} ${lastName}`;
    }
    catch(e){
        console.error(e);
    }
}

function readCookie(name) {
    return decodeURIComponent(document.cookie.replace(new RegExp("(?:(?:^|.*;)\\s*" + name.replace(/[\-\.\+\*]/g, "\\$&") + "\\s*\\=\\s*([^;]*).*$)|^.*$"), "$1")) || null;
  }